<template>
  <div ref="chartContainer" class="trading-chart"></div>
</template>

<script>
import { initTradingView } from '@/lib/tradingview';
import { UDFCompatibleDatafeed } from '@/lib/datafeed';

export default {
  name: 'TradingChart',
  props: {
    symbol: {
      type: String,
      default: 'AAPL'
    },
    interval: {
      type: String,
      default: 'D'
    }
  },
  data() {
    return {
      tvWidget: null
    };
  },
  mounted() {
    this.initChart();
  },
  beforeUnmount() {
    if (this.tvWidget) {
      this.tvWidget.remove();
      this.tvWidget = null;
    }
  },
  methods: {
    async initChart() {
      try {
        this.tvWidget = await initTradingView({
          container: this.$refs.chartContainer,
          symbol: this.symbol,
          interval: this.interval,
          datafeed: new UDFCompatibleDatafeed('http://your-django-api/datafeed'),
          libraryPath: '/static/charting_library/' // Django static path
        });
      } catch (error) {
        console.error('Chart initialization failed:', error);
        this.$emit('error', error);
      }
    }
  }
};
</script>

<style scoped>
.trading-chart {
  width: 100%;
  height: 500px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  margin: 20px auto;
}
</style>