<template>
  <div id="app">
    <h1>My Trading App</h1>
    <TradingChart 
      v-if="!chartError" 
      @error="handleChartError"
    />
    <div v-else class="error-message">
      Failed to load chart. Please refresh the page.
    </div>
  </div>
</template>

<script>
import TradingChart from './components/TradingChart.vue';

export default {
  name: 'App',
  components: {
    TradingChart
  },
  data() {
    return {
      chartError: false
    };
  },
  methods: {
    handleChartError() {
      this.chartError = true;
    }
  }
};
</script>

